// 导航侧边栏
var canviRight = new Canvi({
  content: ".js-canvi-content",
  navbar: ".js-canvi-navbar--right",
  openButton: ".js-canvi-open-button--right",
  position: "right"
});

// top
  $(window).scroll(function(){
        if($(this).scrollTop() > 100){
            $(".top").fadeIn();
      }else{
          $(".top").fadeOut();
      }
  });

  $('.top').click(function(){
      $('html ,body').animate({scrollTop: 0}, 300);
      return false;
  });